import { Divider } from 'antd-mobile'
import NavMobile from '../../../components/mobile/Nav'
import './index.css'

const PolicyMobile = () => {
  const course = [
    {
      id: 1,
      name: '中华人民共和国刑法修正案（八）',
      compulsory: false, // 选修false 必修true
      isStudy: true,
      date: '2022/2/12 15:04:51',
    },
    {
      id: 2,
      name: '中华人民共和国食品安全法实施条例',
      compulsory: false,
      isStudy: true,
      date: '2022/2/12 15:04:51',
    },
    {
      id: 3,
      name: '中华人民共和国行政许可法',
      compulsory: false,
      isStudy: false,
      date: '',
    },
    {
      id: 4,
      name: '中华人民共和国食品安全法（2015年版）',
      compulsory: true,
      isStudy: true,
      date: '2022/2/12 15:04:51',
    },
  ]

  const handleClick = () => {}

  return (
    <div>
      <NavMobile title="政策法规学习" />
      <div className="policy-mobile-seize"></div>
      <div className="policy-mobile">
        {course.map((item, index) => (
          <div className="policy-mobile-card-box" key={item.id}>
            <div className="policy-mobile-card" onClick={handleClick}>
              <p>序号：{index + 1}</p>
              <p>
                课程名称：<span>{item.name}</span>
              </p>
              <p>选修/必修：{item.compulsory ? '必修' : '选修'}</p>
              <p>学习情况：{item.isStudy ? '已学习' : '未学习'}</p>
              <p>学习时间：{item.date}</p>
            </div>
            <Divider className="policy-mobile-card-divider" />
          </div>
        ))}
      </div>
    </div>
  )
}

export default PolicyMobile
